.download{
  height: 300px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  background:#F2F2F2;
  padding-bottom: 30px;
}

.title{
  border-bottom: 3px solid #ED4630;
  width: 400px;
  padding-bottom: 10px;
  position: relative;
}
@media screen and (min-width: 1200px){
  .title:after{
    content:'a';
    position: absolute;
    top: 130%;
    left: 19%;
    display: inline-block;
    width: 250px;
    height: 3px;
    background: #ED4630;
    color:rgba(0,0,0,0);
  } 
}

@-webkit-keyframes rotateUp{
  from{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    bottom:25px;
  }
  to{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    bottom:42px;
  }
}

@keyframes rotateUp{
  from{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    bottom:25px;
  }
  to{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    bottom:42px;
  }
}

@-webkit-keyframes rotateDown{
  from{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    bottom:42px;
  }
  to{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    bottom:25px;

  }
}

@keyframes rotateDown{
  from{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    bottom:42px;
  }
  to{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    bottom:25px;

  }
}

.rotateUp{
  -webkit-animation-name: rotateUp;
          animation-name: rotateUp;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
  bottom:42px !important;
}

.rotateDown{
  -webkit-animation-name: rotateDown;
          animation-name: rotateDown;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
  bottom:25px !important;
}

.icon{
  position: relative;
  bottom: 25px;
  cursor: pointer;
  color:#ED4630;
}

.slick-active{
  display: -ms-flexbox !important;
  display: flex !important;
}

.aviso{
  font-weight: bold;
  font-size: 1.2em;
}
.tHead{
  background-color: #750310;
}
.thHead{
  border-right: 3px solid #FFF;
  padding: 15px;
  color: #FFF;
}
.trWhite{
  padding: 15px;
  text-align: center;
}
.trGrey{
  padding: 15px;
  text-align: center;
  background-color: #EBEBEB; 
}


